html, body, #root, .main {
  height: 100%;
}

.main {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #835540;
  background-image: url(./background.png);
  background-size: 25%;
}

.stage {
  border: 5px solid;
  border-radius: 5px;
}

@media screen and (max-width: 820px) {
  .stage {
    border: none;
    border-radius: 0;
  }
}